// vendor
@import "../../src/scss/vendor/swiper-bundle.min.css";
@import "../../src/scss/vendor/datepicker.minimal.css";
@import "/dist/css/lightcase.css";

// vars
@import "./variables";

// layouts
@import "./layouts/header";
@import "./layouts/nav";
@import "./layouts/footer";
@import "./layouts/modal";

*,
*::before,
*::after {
    box-sizing: border-box;
}

html {
    scroll-behavior: smooth;
    -webkit-overflow-scrolling: touch;
    overflow: auto;
}

body {
    font-family: "Geologica", sans-serif;
    font-size: 16px;
    font-style: normal;
    font-weight: 300;
    line-height: 1.25;
    color: #585a60;
    background-color: #ffffff;
}

.menu-open {
    position: static;
    pointer-events: fill;
    touch-action: none;
    overflow: hidden;
}

.wrapper {
    position: relative;
    display: flex;
    flex-direction: column;
    overflow-x: clip;
}

.content {
    position: relative;
    z-index: 1;
    display: flex;
    flex-direction: column;
    width: 100%;
    flex: 1;
}

.section {
    contain: layout;
}

.wow {
    visibility: hidden;
}

#mysprite {
    display: none;
}

// main page styles start
// main part start
.section-main {
    position: relative;
    z-index: 1;
    display: block;
    width: 100%;
    margin-top: -70px;

    @media (min-width: 1200px) {
        margin-top: -150px;
    }

    @media (min-width: 1400px) {
        margin-top: -160px;
    }

    &-swiper {
        position: relative;
        z-index: 0;
        width: 100%;
        overflow: hidden;

        @media (min-width: 720px) and (max-width: 1199.98px) {
            order: 0;
        }

        & .swiper-main {
            display: flex;
            flex-direction: column;
        }

        & .swiper-slide {
            display: flex;
            width: 100%;

            @media (min-width: 0) and (max-width: 719.98px) {
                aspect-ratio: 9 / 16;
                max-height: 555px;
            }

            @media (min-width: 720px) and (max-width: 1199.98px) {
                max-height: 792px;
            }

            & > * {
                flex: 1;
                width: 100%;
                height: 100%;
                object-fit: cover;

                & * {
                    width: 100%;
                    height: 100%;
                    object-fit: cover;
                }
            }
        }

        & .swiper-pagination {
            position: relative;
            z-index: 1;
            top: 0px;
            margin-top: -24px;
            background-color: rgba(255, 255, 255, 0.6);

            @media (min-width: 720px) and (max-width: 1199.98px) {
                order: 2;
                margin-top: 0;
            }

            @media (min-width: 1200px) {
                // top: -30px;
                top: unset;
                margin-top: -30px;
                background-color: transparent;
            }
        }

        & .card-advantage {
            position: relative;
            z-index: 1;
            display: flex;
            flex-direction: column;
            align-items: center;
            justify-content: center;
            flex-wrap: wrap;
            row-gap: 8px;
            column-gap: 8px;
            width: 100%;
            height: auto;
            min-height: 200px;
            padding: 24px;
            opacity: 1;
            background-color: #eef5fe;
            backdrop-filter: blur(7.95px);
            border: 1px solid #eef5fe;
            pointer-events: all;
            transition: opacity 0.4s ease;

            @media (min-width: 720px) {
                align-items: flex-start;
                justify-content: flex-start;
                row-gap: 10px;
                column-gap: 10px;
                min-height: 265px;
            }

            @media (min-width: 1200px) {
                background-color: rgba(255, 255, 255, 0.85);
                border: 1px solid rgba(255, 255, 255, 0.85);
                max-width: 40%;
                min-height: 235px;
                border-radius: 10px;
            }

            @media (min-width: 1400px) {
                padding: 50px 40px;
            }

            & .card-title {
                font-style: normal;
                font-weight: 400;
                font-size: 20px;
                line-height: 28px;
                text-align: center;
                color: var(--blue);

                @media (min-width: 720px) {
                    font-size: 36px;
                    line-height: 42px;
                    text-align: left;
                }
            }

            & .card-body {
                font-style: normal;
                font-weight: 300;
                font-size: 14px;
                line-height: 18px;
                text-align: center;
                color: var(--black);
                flex: 1;

                @media (min-width: 720px) {
                    font-size: 27px;
                    line-height: 34px;
                    text-align: left;
                }

                & p {
                    margin: unset;
                    text-wrap: balance;
                }
            }
        }
    }

    &-content {
        position: relative;
        z-index: 1;
        width: 100%;
        height: 100%;
        pointer-events: none;

        @media (min-width: 720px) and (max-width: 1199.98px) {
            order: 1;
            margin-top: -16px;
        }

        @media (min-width: 1200px) {
            position: absolute;
            top: 0;
            left: 0;
            height: calc(100% - 24px);
        }

        & > .container {
            position: relative;
            display: flex;
            flex-direction: column;
            height: 100%;
            align-items: flex-start;
            justify-content: flex-end;
            row-gap: 30px;
            padding: 0;

            @media (min-width: 1200px) {
                padding: 30px 0 34px;
            }

            & > .btn {
                position: absolute;
                top: -170px;
                left: 50%;
                white-space: nowrap;
                pointer-events: all;
                transform: translate(-50%, 100%);

                @media (min-width: 720px) {
                    display: none;
                }
            }
        }
    }
}

.excellence-list {
    display: none;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    column-gap: 20px;
    width: 100%;

    @media (min-width: 1200px) {
        display: flex;
    }
}

.card-excellence {
    position: relative;
    z-index: 1;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: flex-start;
    flex-wrap: nowrap;
    row-gap: 20px;
    column-gap: 20px;
    max-width: 700px;
    width: 100%;
    height: auto;
    padding: 16px;
    background-color: rgba(17, 118, 234, 0.4);
    border-radius: 10px;
    border: 1px solid #ffffff;
    backdrop-filter: blur(7.95px);
    font-weight: 300;
    font-size: 16px;
    line-height: 20px;
    color: #ffffff;
    overflow: hidden;

    @media (min-width: 1600px) {
        padding: 16px 24px;
        font-size: 22px;
        line-height: 28px;
    }

    & i {
        font-size: 32px;
        color: #ffffff;

        @media (min-width: 1600px) {
            font-size: 50px;
        }
    }
}

// main part end

// section services part start
.section-services {
    position: relative;
    z-index: 1;
    width: 100%;
    padding: 40px 0;

    @media (min-width: 720px) {
        padding: 60px 0;
    }

    @media (min-width: 1200px) {
        padding: 80px 0;
    }

    & h1,
    & h2 {
        margin: unset;
    }

    & h1 {
        font-style: normal;
        font-weight: 400;
        text-align: center;
        color: var(--blue);
    }

    & h2 {
        font-style: normal;
        font-weight: 400;
        font-size: 20px;
        text-align: center;
        color: #585a60;
        margin-top: 8px;

        @media (min-width: 720px) {
            margin-top: 12px;
        }

        @media (min-width: 1400px) {
            margin-top: 5px;
        }
    }
}

.services-list {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-template-rows: auto;
    row-gap: 7px;
    column-gap: 5px;
    margin-top: 18px;

    @media (min-width: 576px) {
        grid-template-columns: repeat(3, 1fr);
    }

    @media (min-width: 720px) {
        grid-template-columns: repeat(3, 1fr);
        row-gap: 12px;
        column-gap: 12px;
        margin-top: 25px;
    }

    @media (min-width: 1200px) {
        row-gap: 20px;
        column-gap: 20px;
        margin-top: 45px;
    }

    // &.main {
    //   & > * {
    //     &:last-of-type {
    //       display: flex;

    //       @media (min-width: 576px) {
    //         display: none;
    //       }
    //     }
    //   }
    // }
}

.card-service {
    position: relative;
    z-index: 1;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    // row-gap: 14px;
    // column-gap: 14px;
    min-width: 48%;
    min-height: 135px;
    border: 1px solid rgba(44, 53, 109, 0.3);
    border-radius: 10px;
    padding: 6px 7px;
    text-decoration: none;
    transition: 0.4s ease;

    @media (min-width: 720px) {
        padding: 23px 28px;
    }

    @media (min-width: 1400px) {
        padding: 23px 28px;
        // row-gap: 40px;
        // column-gap: 40px;
    }

    & .stretched-link {
        position: absolute;
        top: 0;
        left: 0;
        z-index: 1;
        width: 100%;
        height: 100%;
    }

    & .card-title {
        order: 0;
        flex: 1 0 auto;
        width: 100%;

        @media screen and (max-width: 719.98px) {
            word-break: break-word;
            word-wrap: break-word;
        }

        @media (min-width: 1200px) {
            flex: 1 0 65%;
        }

        & div {
            &:first-of-type {
                font-style: normal;
                font-weight: 400;
                font-size: 18px;
                line-height: 1.25;
                color: var(--blue);

                @media (min-width: 1200px) {
                    font-size: 20px;
                }

                @media (min-width: 1400px) {
                    font-size: 24px;
                }
            }

            &:last-of-type {
                font-style: normal;
                font-weight: 300;
                font-size: 14px;
                line-height: 1.25;
                color: #585a60;

                @media (min-width: 1200px) {
                    font-size: 16px;
                }

                @media (min-width: 1400px) {
                    font-size: 20px;
                }
            }
        }
    }

    & svg {
        color: #585a60;
        order: 3;
        width: 46px;
        height: 46px;
        flex: 0 1 auto;
        align-self: flex-end;
        object-fit: contain;
        transition: 0.4s ease;

        @media (min-width: 720px) {
            align-self: flex-start;
            margin-left: auto;
        }

        @media (min-width: 1200px) {
            order: 1;
            width: 56px;
            height: 56px;
        }

        @media (min-width: 1400px) {
            width: 90px;
            height: 90px;
        }
    }

    & .card-footer {
        position: relative;
        z-index: 2;
        display: flex;
        flex-direction: row;
        align-items: center;
        justify-content: space-between;
        row-gap: 10px;
        column-gap: 10px;
        order: 2;
        width: 100%;
        flex: 1;
        align-self: flex-end;
        pointer-events: all;

        @media (min-width: 720px) {
            padding-top: 5px;
        }

        & div {
            font-style: normal;
            font-weight: 400;
            font-size: 14px;
            line-height: 17.5px;
            color: var(--blue);
            flex: 1 0 auto;

            @media (min-width: 720px) {
                font-size: 18px;
                line-height: 22px;
            }

            @media (min-width: 1400px) {
                font-size: 32px;
                line-height: 40px;
            }
        }

        & .btn {
            position: relative;
            z-index: 2;
            display: none;
            pointer-events: all;

            @media (min-width: 1200px) {
                display: inline-block;
                padding: 13px 30px;
                opacity: 0;
                visibility: hidden;
            }

            @media (min-width: 1400px) {
                padding: 13px 60px;
            }
        }
    }

    &:hover {
        background-color: var(--blue);
        border-color: var(--blue);
        box-shadow: 3px 3px 11.8px 2px rgba(81, 81, 81, 0.48);

        & svg {
            color: #ffffff;

            & * {
                fill: currentColor;
                stroke: currentColor;
            }
        }

        & .card-title,
        & .card-footer {
            & div {
                color: #ffffff;
            }
        }

        & .btn {
            @media (min-width: 1200px) {
                opacity: 1;
                visibility: visible;
            }
        }
    }

    &:nth-last-child(1) {
        & svg {
            max-width: 50%;
            max-height: 50%;
        }
    }
}

// section services part end

// section doctors part start
.section-doctors {
    position: relative;
    padding: 40px 0;
    background-color: rgba(17, 118, 234, 0.07);
    overflow: hidden;

    @media (min-width: 720px) {
        padding: 60px 0;
    }

    @media (min-width: 1200px) {
        padding: 80px 0;
    }

    & > .container {
        & > h1,
        & > h2 {
            margin: 0 auto;
            // text-align: center;
        }
    }

    & .swiper-doctors {
        position: relative;
        z-index: 1;
        margin-top: 18px;

        @media (min-width: 720px) {
            margin-top: 31px;
        }

        @media (min-width: 1200px) {
            margin-top: 22px;
        }

        & .swiper-container {
            position: relative;
            z-index: 1;
        }

        & .swiper-wrapper {
            // width: fit-content;
            margin: 0 auto;
            align-items: stretch;
        }

        & .swiper-slide {
            position: relative;
            z-index: 1;
            height: auto;
            // width: fit-content !important;
        }

        & .swiper-pagination {
            position: relative;
            top: unset;
            bottom: unset;
            left: unset;
            margin-top: 26px;

            @media (min-width: 720px) {
                margin-top: 36px;
            }

            @media (min-width: 1200px) {
                margin-top: 40px;
            }
        }
    }
}

.doctors-list {
    display: grid;
    grid-template-columns: 1fr;
    grid-template-rows: auto;
    row-gap: 20px;
    column-gap: 20px;

    // margin-top: 20px;
    @media (min-width: 576px) {
        grid-template-columns: repeat(2, 1fr);
    }

    @media (min-width: 720px) {
        padding: 0 10%;
        // margin-top: 35px;
    }

    @media (min-width: 992px) {
        grid-template-columns: repeat(3, 1fr);
        padding: 0 5%;
    }

    @media (min-width: 1400px) {
        grid-template-columns: repeat(4, 1fr);
        padding: 0;
    }
}

.card-doctor {
    position: relative;
    z-index: 1;
    display: flex;
    flex-direction: column;
    width: 100%;
    height: 100%;
    background-color: #ffffff;
    border-radius: 10px;
    padding-bottom: 25px;
    overflow: hidden;
    transition: 0.4s ease;
    // max-width: 280px;
    text-decoration: none;

    @media (min-width: 720px) {
        // width: 340px;
        // max-width: 100%;
        padding-bottom: 17px;
    }

    &-image {
        position: relative;
        z-index: 1;
        width: 100%;
        // height: 100%;
        aspect-ratio: 1 / 1.25;
        border-radius: 10px;
        overflow: hidden;

        & > * {
            width: 100%;
            height: 100%;
            object-fit: cover;
            object-position: top center;
        }
    }

    & .card-content {
        display: flex;
        flex-direction: column;
        width: 100%;
        height: 100%;
        flex: 1;
    }

    & p {
        font-style: normal;
        font-weight: 400;
        font-size: 18px;
        line-height: 22px;
        color: var(--blue);
        margin: 0;
        padding: 25px 20px 0 20px;

        @media (min-width: 720px) {
            font-size: 18px;
            line-height: 22px;
        }

        @media (min-width: 1200px) {
            font-size: 22px;
            line-height: 27.5px;
            padding: 30px 20px 0 20px;
        }
    }

    &-meo {
        font-style: normal;
        font-weight: 400;
        font-size: 14px;
        line-height: 18px;
        color: #585a60;
        padding: 4px 20px 0 20px;

        @media (min-width: 720px) {
            font-size: 16px;
            line-height: 20px;
        }
    }

    &-experience {
        font-style: normal;
        font-weight: 300;
        font-size: 14px;
        line-height: 18px;
        color: var(--blue);
        padding: 18px 20px 0 20px;
        margin-top: auto;

        @media (min-width: 720px) {
            font-size: 18px;
            line-height: 22.5px;
        }
    }

    &:hover {
        box-shadow: 0px 4px 13px rgba(0, 0, 0, 0.25);

        & p {
            text-decoration: underline;
            text-decoration-thickness: 2px;
            text-decoration-skip-ink: none;
            text-decoration-skip: 10px;
        }
    }
}

// section doctors part end

// section technologies part start
.section-technologies {
    position: relative;
    z-index: 1;
    width: 100%;
    overflow: hidden;
    padding: 40px 0;

    @media (min-width: 720px) {
        padding: 60px 0;
    }

    @media (min-width: 1200px) {
        padding: 80px 0;
    }

    & > h2 {
        margin-top: 0;
        text-align: center;
    }

    & .swiper-technologies {
        position: relative;
        z-index: 1;
        display: flex;
        flex-direction: column;
        margin-top: 12.78px;

        @media (min-width: 720px) {
            margin-top: 30px;
        }

        @media (min-width: 1200px) {
            margin-top: 25px;
        }
    }

    & .swiper-pagination {
        position: relative;
        z-index: 1;
        top: 0px;
        margin-top: 0;
        background-color: rgba(255, 255, 255, 0.6);
        order: 2;

        @media (min-width: 720px) {
            margin-top: 35px;
        }

        @media (min-width: 1200px) {
            margin-top: 27px;
        }
    }

    & .swiper-slide {
        display: flex;
        width: 100%;
        max-height: 320px;
        aspect-ratio: 1 / 1;

        @media (min-width: 720px) {
            aspect-ratio: 2 / 1.5;
            max-height: 491px;
        }

        @media (min-width: 1200px) {
            max-height: 585px;
        }

        & > * {
            flex: 1;
            width: 100%;
            height: 100%;
            object-fit: cover;
            object-position: bottom;

            & * {
                width: 100%;
                height: 100%;
                object-fit: cover;
                object-position: bottom;
            }
        }
    }

    &-content {
        position: relative;
        z-index: 1;
        width: 100%;
        height: 100%;
        pointer-events: none;

        @media (min-width: 720px) and (max-width: 1199.98px) {
            order: 1;
            margin-top: -16px;
        }

        @media (min-width: 1200px) {
            position: absolute;
            top: 0;
            left: 0;
        }

        & > .container {
            position: relative;
            display: flex;
            flex-direction: column;
            height: 100%;
            align-items: flex-start;
            justify-content: center;
            row-gap: 30px;

            @media (min-width: 1200px) {
                padding: 20px 0;
            }
        }
    }
}

.card-tech {
    position: relative;
    z-index: 1;
    display: flex;
    flex-direction: column;
    opacity: 1;
    width: 100%;
    // height: 496px;
    height: 380px;
    padding: 30px 18px;
    background-color: #ffffff;
    overflow: hidden;
    transition: 0.05s opacity 0.4s ease-in-out;

    @media (min-width: 720px) {
        border-radius: 10px;
        height: 275px;
        background-color: #eef5fe;
        padding: 36px 50px;
    }

    @media (min-width: 1200px) {
        max-width: 560px;
        height: 490px;
        padding: 85px 50px;
        background-color: rgba(255, 255, 255, 0.8);
        backdrop-filter: blur(11.9px);
    }

    & .card-title {
        font-style: normal;
        font-weight: 400;
        font-size: 18px;
        line-height: 22px;
        color: #585a60;
        text-transform: uppercase;

        @media (min-width: 720px) {
            font-size: 24px;
            line-height: 30px;
        }

        @media (min-width: 1200px) {
            font-size: 32px;
            line-height: 40px;
        }
    }
}

// section technologies part end

// section smile part start
.section-smile {
    position: relative;
    z-index: 1;
    padding: 40px 0;
    overflow: hidden;

    @media (min-width: 720px) {
        padding: 60px 0;
    }

    @media (min-width: 1200px) {
        padding: 80px 0;
    }

    & .container {
        display: flex;
        flex-direction: column;
        align-items: stretch;

        & > h2 {
            margin: 0;
            // text-align: center;
        }

        & .btn {
            width: 100%;
            margin: 50px auto 0;

            @media (min-width: 576px) {
                width: 52.5vw;
            }

            @media (min-width: 720px) {
                margin: 55px auto 0;
            }

            @media (min-width: 992px) {
                width: 37vw;
            }

            @media (min-width: 1200px) {
                width: 30.5vw;
            }

            @media (min-width: 1680px) {
                width: 517px;
            }
        }
    }

    & .swiper-smiles {
        margin-top: 16px;

        @media (min-width: 720px) {
            margin-top: 34px;
        }

        @media (min-width: 1200px) {
            max-width: 100%;
            margin-top: 40px;
        }

        & .swiper-wrapper {
            align-items: stretch;
            margin: 0 auto;
        }

        & .swiper-slide {
            position: relative;
            z-index: 1;
            // width: fit-content !important;
        }

        & .swiper-pagination {
            position: relative;
            top: unset;
            bottom: unset;
            left: unset;
            margin-top: 26px;

            @media (min-width: 720px) {
                margin-top: 36px;
            }

            @media (min-width: 1200px) {
                margin-top: 40px;
            }
        }
    }
}

.smile-card {
    position: relative;
    z-index: 1;
    display: block;
    width: 100%;
    height: 100%;

    & > p {
        margin-top: 0;
    }

    & .smile-title {
        font-style: normal;
        font-weight: 400;
        font-size: 14px;
        line-height: 18px;
        color: #585a60;

        @media (min-width: 720px) {
            font-size: 22px;
            line-height: 27.5px;
        }
    }

    & .smile-images {
        position: relative;
