    & > .container {
        display: grid;
        grid-template-columns: 1fr;
        row-gap: 20px;
        column-gap: 20px;

        @media (min-width: 1200px) {
            grid-template-columns: repeat(2, 1fr);
            grid-template-rows: repeat(auto, 100%);
            row-gap: 20px;
            column-gap: 40px;
