                    & p {
                        margin: 0 auto;
                        max-width: 80%;
                        text-align: left;
                    }
                }

                @media (min-width: 1200px) {
                    grid-column-start: 1;
                    grid-column-end: 3;

                    & p {
                        margin: 0 auto;
                        max-width: 60%;
                    }
                }
            }

            &:nth-child(2) {
                & p {
                    margin: unset;
                }
            }
        }
    }

    & table {
        width: 100%;
        height: fit-content;
        border-collapse: collapse;

        & tr {
            transition: 0.4s ease;

            &:nth-child(odd) {
                background-color: #eef5fe;
            }

            &:nth-child(even) {
                background-color: #ffffff;
            }

            &:hover {
                background-color: #dadcdf;

                & a {
                    text-decoration: none;
                }
            }

            & td {
                color: #585a60;
                padding: 17px 10px 17px 20px;

                @media (min-width: 720px) {
                    padding: 17px 10px 17px 50px;
                }

                @media (min-width: 1200px) {
                    padding: 17px 10px 17px 84px;
                }

                &:last-of-type {
                    padding: 17px 20px 17px 10px;

                    @media (min-width: 720px) {
                        padding: 17px 50px 17px 10px;
                    }

                    @media (min-width: 1200px) {
                        padding: 17px 84px 17px 10px;
                    }
                }

                & a {
                    display: block;
                    width: 100%;
                    height: 100%;
                    color: inherit;
                }

                &:last-of-type {
                    text-align: left;
                    white-space: nowrap;
                }
            }
        }
    }

