            flex-direction: column;
        }

        & > * {
            width: 100%;
            height: 100%;
            object-fit: cover;
        }
    }

    & .result-card {
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        height: 100%;
        padding: 20px 5px 30px;
        border: 1px solid rgba(44, 53, 109, 0.3);
        border-radius: 10px;
        background: #ffffff;

        @media (min-width: 720px) {
            padding: 38px 12px 30px;
        }

        @media (min-width: 1200px) {
            padding: 45px 10% 30px;
        }

        @media (min-width: 1400px) {
            padding: 45px 15% 30px;
        }

        & svg,
        & img {
            width: fit-content;
            height: 53px;
            object-fit: contain;
            margin-bottom: 13px;

            @media (min-width: 720px) {
                height: 75px;
            }
        }

        & p {
            margin: unset;
            font-size: 14px;
            line-height: 1.25;
            text-align: center;

            @media (min-width: 720px) {
                font-size: 16px;
                line-height: 20px;
            }
        }
    }
}

